import React from "react";
import CardSection from "@src/components/Card/CardSection";
import { cardInfo } from "@src/interfaces/common";
import { Link } from "react-router-dom";



const experiences: cardInfo[] = [
  {
    title: "Amiti Software Technologies Pvt. Ltd.",
    subTitle: "Software Developer",
    duration: "June 2021 - Present",
    details: [
      "Built reusable UI components using ReactJS and TypeScript for scalable and consistent application design.",
      "Managed state efficiently with Redux, ensuring smooth data flow across modules.",
      "Designed and integrated REST APIs using ASP.NET Core, handling business logic and service layers.",
      "Developed responsive UIs using CSS Grid, Flexbox for better user experience.",
      "Ensured code quality through Jest unit testing and regular code reviews.",
    ],
    linkTitle:'View Professional Projects',
    url:'/projects'
  },
];


const Experience: React.FC = () => {
  return (
    <div className="experience-page"
      style={{ padding: '2.5rem', backgroundColor: '#f4f8fb' }}>
      <CardSection
        title="Professional Experience"
        subtitle="Building Scalable and Efficient Applications"
        cardInfoList={experiences}
      />
    </div>
  );
};

export default Experience;


